.front {
  top: 10%;
  width: 100%;
  max-width: 1200px;
  background-color: rgba(74, 74, 74, 0.862);

  color: white;
  position: fixed;
  z-index: 2;
  padding: 0 1%;
}

.back {
  width: 100%;
  height: 100vh;
  background-color: transparent;
  position: absolute;
  z-index: 1;
}

.modalContent {
  display: flex;
  justify-content: space-around;
  border-radius: 10px;
  margin: 10px 0;
  background-color: rgb(216, 212, 212);
color: black;
}

.button {
  position: absolute;
  padding: 10px 23px;
  bottom: 0;
  right: 0;
}

#butt {
  right: 80px;
}

.h3 {
  width: fit-content;
}
