.modalContent {
  display: flex;
  align-items: center;
  justify-content: space-around;
  border-radius: 10px;
  margin: 10px 0;
  background-color: silver;
}

.button {
  padding: 10px 23px;
  bottom: 0;
  right: 0;
}

.time {
  background-color: rgb(71, 71, 149);
}

.time select {
  font-size: 44px;
}

.creditCard {
  background-color: red;
}

.timeCreditCard {
  display: flex;
  justify-content: space-around;
}

@media screen and (max-width: 600px) {
  .timeCreditCard {
    display: grid;
  }
}

.payment img {
  width: 100px;
  height: 80px;
  object-fit: cover;
}
