.icon {
  border-radius: 50px;
  border: 1px solid red;
  font-size: 25px;
  padding: 4px;
  cursor: pointer;
  background-color: black;
  color: white;
}

.icons {
  top: 20px;
  display: flex;
  align-items: center;
  background-color: transparent;
  position: absolute;
}

.p {
  top: 45px;
  color: red;
  border-radius: 0 0 10px;
  position: absolute;
  padding: 4px;
  background-color: black;
}

@media screen and (max-width: 600px) {
  .icons {
    top: 60px;
    /* display: grid; */
    /* padding-left: 90%; */
  }
  .icon {
    font-size: 20px;
  }
  .p {
    top: 75px;
    padding: 1px;
  }
}

.images {
  display: grid;
  justify-content: center;
  /* width: 100%; */
}

.header img {
  width: 100%;
  /* max-width: 900px; */
  min-height: 60px;
  max-height: 100px;
  object-fit: cover;
}

.link {
  background-color: transparent;
  display: flex;
  align-items: center;
}
