.link {
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 10px;
  border: 1px solid red;
  color: red;
}

.link img {
  width: 100px;
  height: 100px;
  object-fit: cover;
  border-radius: 50px;
}

@media screen and (max-width: 600px) {
  .link img {
    width: 100px;
    height: 100px;
  }
}
