.imagesAndBox {
  display: flex;
  flex-wrap: wrap;
}

.information {
  font-family: "Courier New", Courier, monospace;
  font-size: x-large;
  margin-left: 10px;
}

.images {
  display: grid;
}

.one {
  width: 300px;
  height: 400px;
  object-fit: cover;
}

.three {
  display: grid;
  width: 100px;
  height: 100px;
  object-fit: cover;
}

.threeDiv {
  display: flex;
}

.oneDiv {
  display: flex;
  margin: 5px 0;
}

.oneDiv :hover {
  margin-left: 30%;
  transition: 0.7s;
  transform: scale(1.5);
  z-index: 1;
}

.threeDiv :hover {
  margin-left: 15%;
  transition: 0.7s;
  transform: scale(2);
  z-index: 1;
}
